import Vue from 'vue';
import Vuex from 'vuex';

import Nedb from 'nedb';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    stockItems: null,
    salesItems: null,
    dbstock: new Nedb({filename: "a-stock.db", autoload: true}),
    dbsales: new Nedb({filename: "a-sales.db", autoload: true}),
  },
  mutations: {
    SETSTOCKITEMS(state, payload) {
      state.stockItems = payload;
    },
    INSERTSALES(state, payload) {
      state.salesItems = payload;
    },
    RETURNSALES() {

    }
  },
  actions: {
    setStockItems({state,commit}, payload=null) {
      return new Promise((resolve, reject) => {
        // INSERT INTO STOCK    *********************** 
        if(!payload) {
          state.dbstock.find({}, (err, docs) => {
            if(err) reject("Data is empty!");
            else resolve(docs);
          });
          
        }else {
          state.dbstock.insert(payload, (err, newDoc) => {
            if(err) reject(err);
            else {
              // IF INSERT IS SUCCESSFUL, RETURN THE NEWDOC & ALL DOCS
              state.dbstock.find({}, (err, docs) => {
                if(err) reject(err);
                else {
                  commit("SETSTOCKITEMS", newDoc);
                  resolve([newDoc, docs]);
                }
              });
            }
          });
        }

        
      });
    },
    insertSales({state,commit}, payload = null) {
      return new Promise((resolve, reject) => {
        if(!payload) reject("Sales Items is empty!")
        else {
          state.dbsales.insert(payload, (err, newDocs) => {
            if(err) reject(["Insert Didn't work as expected", err]);
            else {
              resolve(newDocs);
              commit("INSERTSALES", newDocs);
            }
          });
        }
      });
    },
    returnSales({state,commit}, payload = null) {
      return new Promise((resolve, reject) => {
        if(payload) {
          console.log("docs");
          
        }else {
          state.dbsales.find({}, (err, docs) => {
            if(err) reject(err);
            else {
              resolve(docs);
              commit("RETURNSALES");
            }
          });
        }
      });
    }
  },
});
