











import { tooltip } from '../../inserts/erfunctions';

export default {
props: ['text', 'show'],
mounted() {
  this.showme();
  this.tooltipState = this.show;
},
data: () => ({
  msgText: '',
  tooltipState: false,
  timing: 0,
  textTiminig: 0
}),
computed: {
  msg() {
    this.showme(this.text, this.show)
    return this.msgText;
  }
},
methods: {
  showme(msg:string, state:boolean = true) {
    clearTimeout(this.timing);
    this.tooltipState = state;
    this.msgText = msg ? msg : this.text ? this.text : this.tooltipState = false;
    this.timing = setTimeout(() => {
      this.tooltipState = false;
    }, 30000);
  }
},
watch: {
  text(newVal) {
    this.showme(newVal, this.show);
    // console.log(newVal);
  },
  show(newVal) {
    this.showme(this.text, newVal);
    // console.log(newVal);
  }
},
}
