





















































import { mapActions } from 'vuex';

export default {
mounted() {
  this.returnSales().then((sales) => {
    this.sorting("date", sales);
    this.salesItems = sales;
    console.log(sales);
  }).catch((err) => {
    console.log(err);
  });
},
data: () => ({
  salesItems: []
}),
computed: {
  salesTotal() {
    return this.salesItems.reduce((acc, sale) => acc + (+sale.price), 0);
  }
},
methods: {
  ...mapActions(['returnSales']),
  
},
watch: {
  '$route.name'(name) {
    
    if(name == "dashboard") {
      this.returnSales().then((sales) => {
        this.sorting("date", sales);
        this.salesItems = sales;
        // console.log(sales);
      }).catch((err) => {
        console.log(err);
      });
    }
  }
},
}
