





export default {
props: ['v', 'h', 'height'],
mounted() {
  this.chkVH();

},
data: () => ({
  vClass: false,
  hClass: false,
  heightVal: false
}),
methods: {
  chkVH() {
    if(typeof this.v != 'undefined' || this.v != null) this.vClass = true;
    else  this.vClass = false;
    if(typeof this.h != 'undefined' || this.h != null) this.hClass = true;
    else  this.hClass = false;
    if(typeof this.height != 'undefined' || this.height != null) this.heightVal = this.height + 'px';
    else { this.heightVal = '1px';}

    // if(this.vClass == false && this.hClass == false) this.hClass = true;
  }
},
watch: {
  v(newVal) {
    this.chkVH();
  },
  h(newVal) {
    this.chkVH();
  },
  height(newVal) {
    this.chkVH();
  }
},
}
