












export default {
  props: ['size', 'mblock', 'flex', 'l', 'r', 'flip'],
  mounted() {
    this.flipped;
    if(typeof this.mblock == 'string') {
      this.display = 'block';
    }
    if(typeof this.flex == 'string') {
      this.display = 'flex';
    }
    
  },
  data: () => ({
    display: 'inline-block',
  }),
  computed: {
    leftPos():string {
      if(typeof this.l == 'string') {
        if(this.l == ""){
          return '12';
        }else{
          return this.l
        }
      }else {
        return '0';
      }
    },
    rightPos():string {
      if(typeof this.r == 'string') {
        if(this.r == ""){
          return '12';
        }else{
          return this.r
        }
      }else {
        return '0';
      }
    },
    flipped() {
      // console.info(this.flip == null);
      if(this.flip != null) {
        let el = this.$refs.mIcon as HTMLElement;
        let classname = el ? el.getAttribute('class') : null;

        if(classname && classname.search('flip') < 0)
          el.setAttribute('class', classname + ' flip');
        else if(!classname) el.setAttribute('class', 'flip');
      }
    }
  },
  watch: {
    mblock() {
      // console.log(this.mblock);
      if(typeof this.mblock == 'string') {
        this.display = 'block';
      }
      if(typeof this.flex == 'string') {
        this.display = 'flex';
      }

    },
    flex() {
      // console.log(this.flex);
      if(typeof this.flex == 'string') {
        this.display = 'flex';
      }

    },
    flip() {
      this.flipped
    }
  }
}
