import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import Add from './views/Add.vue';
import Dashboard from './views/Dashboard.vue';
import Manage from './views/Manage.vue';
import Account from './views/Account.vue';
import Signings from './views/Signings.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },{
      path: '/add',
      name: 'add',
      component: Add,
    },{
      path: '/dashboard',
      name: 'dashboard',
      component: Dashboard,
    },{
      path: '/manage',
      name: 'manage',
      component: Manage,
    },{
      path: '/account',
      name: 'account',
      component: Account
    },{
      path: '/signin',
      name: 'signin',
      component: Signings,
    },{
      path: '/signup',
      name: 'signup',
      component: Signings,
    },
  ],
});
