




































export default {
mounted() {
  this.miniScreenChk();
  this.menuShowChk();


  window.addEventListener('resize', () => {
    this.miniScreenChk();
  });
},
data: () => ({
  navItems: [{
    name: 'home',
    displayName: 'Home',
    link: '/'
  },{
    name: 'add',
    displayName: 'Add',
    link: '/add'
  },{
    name: 'dashboard',
    displayName: 'Dashboard',
    link: '/dashboard'
  },{
    name: 'manage',
    displayName: 'Manage',
    link: '/manage'
  }],
  menuShow: false,
  miniScreen: false
}),
methods: {
  miniScreenChk() {
    if(window.innerWidth <= 600) {
      this.miniScreen = true;
    }else {
      this.miniScreen = false;
    }
  },
  async menuShowChk() {
    let appWrap = await this.$awaitTimeout(() => document.querySelector('.application--wrap')) as HTMLElement;
    let navDrawer = document.querySelector('.nav-drawer') as HTMLElement;
    let widthNav = navDrawer ? navDrawer.offsetWidth : 260;
    
    if(this.menuShow) {
      appWrap.style.transform = "translateX("+ -widthNav +"px)";
      navDrawer ? navDrawer.style.transform = "translateX("+ widthNav +"px)" : null;

      appWrap.style.transition = "transform 0.35s cubic-bezier(0.46,-0.26, 0.49, 1.21)";
    }else {
      appWrap.style.transform = "translateX("+ 0 +"px)";
      navDrawer ? navDrawer.style.transform = "translateX("+ (widthNav) +"px)" : null;

    }
  }
},
watch: {
  menuShow(state) {
    this.menuShowChk();
  },
  '$route.name'(name) {
    console.log(name);
    this.menuShow = false;
  }
}
}
