},
data: () => ({
  signup: false,
  signupForm: {
    firstname: '',
    lastname: '',
    email: '',
    password: '',
    confirmPassword: '',
    phone: '',
    img: null,
    username: '',
    address: '',
    locationItems: [{
      id: '233',
      title: 'Ghana'
      },{
      id: '2',
      title: 'Canada'
      },{
      id: '1',
      title: 'USA'
      },{
      id: '54',
      title: 'Australia'
    }],
    locationSelected: {
      id: '233',
      title: 'Ghana'
    },
    phoneSelected: {
      id: '233',
      title: 'Ghana'
    },
    errors: []

  },
  signupCheckStrength: false,
  signinForm: {
    username: '',
    password: '',
    rememberMe: false,
    errors: []
  },
  errors: {
    '0': ["Please Your name is required", 'firstname'],
    '1': ["Please, your lastname field cannot be empty", 'lastname'],
    '2': ["Email field is empty.", 'email'],
    '2.1': ["Please enter your email / username.", 'email'],
    '2.3': ["This email is already Registered.", 'email'],
    '2.5': ["Please make sure your email is alright.", 'email'],
    '2.7': ["This email is not Registered.", 'email'],
    '2.8': ["This username is not Registered.", 'email'],
    '3': ["Please Password is Required!", 'password'],
    '3.2': ["Password must be 8 or more characters long!", 'password'],
    '3.3': ["The Password you entered is wrong!", 'password'],
    '3.4': ["Please Confirm the Password!", 'confirmPassword'],
    '3.8': ["Please this does not match your password!", 'confirmPassword'],
    '4': ["Please enter your phone number!", 'phone'],
    '5': ["Please enter your username!", 'username'],
    '5.5': ["This username already exist!", 'username'],
    '5.6': ["Please make sure your username is alright.", 'email'],
    '6': ["Please enter your address!", 'address'],
  },
  errsBool: {
    firstname: false,
    lastname: false,
    email: false,
    password: false,
    confirmPassword: false,
    phone: false,
    username: false,
    address: false,
  },
  modal: {
    text: '',
    info: '',
    state: false
  },
  testObj: {
    name: "",
    list: []
  }
}),
computed: {
  ...mapGetters(['getDomain', 'getServerDomain']),
  errorCheck() {
    // VALIDATION OF SUBMIT FORM  ********************
    // CHECK FOR ERRORS FIRST     ********************
    if(this.$route.name == 'signup') {
      const form = this.signupForm;
      //  SIGN-UP ERROR CHECKING    ****************************
      // ***********************************************************
  
      if(form.firstname == '') {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => error != '0');
        this.signupForm.errors.push('0');
      }else this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => error != '0');
      if(form.lastname == '') {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => error != '1');
        this.signupForm.errors.push('1');
      }else this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => error != '1');
      if(form.email == '') {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => !(error == '2' || error == '2.5'));
        this.signupForm.errors.push('2');
      }else {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => !(error == '2' || error == '2.5'));
        let emailPattern = /^[\dA-Z.a-z]+@[\da-z.A-Z]+[\da-zA-Z]+\.[\da-zA-Z]{1,5}$/;
        // console.log(form.email.search(emailPattern));
        if(form.email.search(emailPattern) < 0) {
          this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => !(error == '2' || error == '2.5'));
          this.signupForm.errors.push('2.5');
        }else this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => !(error == '2' || error == '2.5'));
      }
      if(form.password == '') {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => !(error == '3' || error == '3.2' || error == '3.4' || error == '3.8'));
        this.signupForm.errors.push('3');
      }else if(form.password.length < 8) {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => !(error == '3' || error == '3.2' || error == '3.4' || error == '3.8'));      
        this.signupForm.errors.push('3.2');
  
      }else if(form.confirmPassword == '') {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => !(error == '3' || error == '3.2' || error == '3.4' || error == '3.8'));      
        this.signupForm.errors.push('3.4');
      }else if (form.password != form.confirmPassword) {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => !(error == '3' || error == '3.2' || error == '3.4' || error == '3.8'));
        this.signupForm.errors.push('3.8');
      }
      if(form.phone == '') {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => error != ('4'));
        this.signupForm.errors.push('4');
      }else if(form.phone.search("")) {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => error != ('4'));
      }else this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => error != ('4'));
      if(form.username == '') {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => error != ('5'));
        this.signupForm.errors.push('5');
      }else this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => error != ('5'));
      if(form.address == '') {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => error != ('6'));
        this.signupForm.errors.push('6');
      }else this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => error != ('6'));
      
      return this.signupForm.errors;

    }else {
      //  SIGN-IN ERROR CHECKING    ****************************
      // ***********************************************************
      const form = this.signinForm;
      form.errors = [];

      // USERNAME OR EMAIL
      if(form.username == '') {
        form.errors.push('2.1');
      }else {
        let atChkPattern = /@/;
        let emailPattern = /^[\dA-Z.a-z]+@[\da-z.A-Z]+[\da-zA-Z]+\.[\da-zA-Z]{1,5}$/;
        let usernamePattern = /^[\dA-Z.a-z]+$/;
        
        if(form.username.search(atChkPattern) >= 0) {
          if(form.username.search(emailPattern) < 0) form.errors.push('2.5');
        }
        // if(form.username.search(usernamePattern) >= 0) form.errors.push('5.6');
      }
      if(form.password == '') {
        form.errors.push('3');
      }else if(form.password.length < 8) {
        form.errors.push('3.2');
  
      }

      return form.errors;
    }

    // if(this.signupForm.errors.length > 0) return console.log(this.signupForm.errors);

    // console.log(this.signupForm.errors && this.signupForm.errors.filter(val => val.search(/(3\.4)+(3\.8)+/) > -1));
    // console.log(this.signupForm.errors && this.signupForm.errors.filter(val => val.search(/(3$)+/) > -1));
    // console.log(this.signupForm.errors);
    
  },
  returnErrMsg() {
    return (errs) => {
      // console.log(errs);
      let errPattern = '';
      errs.forEach(n => {
        if(errPattern == '') errPattern += "(^" + n + "$)+";
        else errPattern += "|(^"+ n +"$)+";
      });
      
      let errStr = this.errorCheck && this.errorCheck.filter(val => val.search(errPattern) > -1)[0];
      return this.errors[errStr] && this.errors[errStr][0];
      // console.log(errStr);
      
    }
  }
},
methods: {
  signupSubmit(e:Event) {
    console.log(this.signupForm.errors);
    // this.passwordPrompt();
    if((this.signupForm.errors && this.signupForm.errors.length)) {
      this.signupForm.errors.forEach(err => {
        let errName = this.errors[err] && this.errors[err][1];
        this.showing([err], errName);
      });
    }else {
      let formData = this.$extFD({
        username: this.signupForm.username,
        identifier: 'username'
      });
      this.$axios.post(this.getServerDomain + "/users/get", formData).then((res) => {
        if((res.data && res.data.length == 0)) {
          // IF USERNAME DOESNOT EXIST THEN PROCEED SIGNING UP
          let sendSignup = this.$extFD({
            firstname: this.signupForm.firstname,
            lastname: this.signupForm.lastname,
            email: this.signupForm.email,
            password: this.signupForm.password,
            phone: this.signupForm.phoneSelected.id + "-" + this.signupForm.phone.replace(/^0/,""),
            username: this.signupForm.username,
            address: this.signupForm.address,
            country: this.signupForm.locationSelected.title,
          });
          console.log({...this.signupForm});

          this.$axios.post(this.getServerDomain + "/users/signup", sendSignup).then((afterSignup) => {
            console.log(afterSignup.data, "Sign-up was Successful!");
          }).catch((errSignup) => {
            console.log(errSignup);
          });
        }else {
          console.log("Username Already exists!");
          this.signupForm.errors.join("-").search("5.5") < 0 && this.signupForm.errors.push("5.5");
          console.log(this.signupForm.errors);
          this.showing(['5.5'], "username", true);
        }
      }).catch((err) => {
        console.log("Sign-up error",err);
      });
    }

  },
  signinSubmit(e:Event) {
    let form = this.signinForm;
    if((form.errors && form.errors.length)) {
      form.errors.forEach(err => {
        let errName = this.errors[err] && this.errors[err][1];
        this.showing([err], errName);
      });
      // console.log(form.errors);
    }else {
      let formDataObj = {
        password: form.password
      };
      form.username.search("@") > -1 ? formDataObj['email'] = form.username : formDataObj['username'] = form.username;
      
      let formData = this.$extFD(formDataObj);
      
      // SIGN-IN RETURNED RESPONSE    ************************************
      this.$axios.post(this.getServerDomain + "/users/signin", formData).then((res) => {
        if(res.data && res.data[0] == 'username') {
          this.modal.info = "The username you entered does not exist!";
          this.modal.state = true;
        }else if(res.data && res.data[0] == 'email') {
          this.modal.info = "The email you entered does not exist!";
          this.modal.state = true;
        }else if(res.data && res.data[0] == 'password') {
          this.modal.info = "The password is wrong!";
          this.modal.state = true;
        }else if(res.data) {
          console.log(res.data);
          this.animateText(this.testObj, "-Successful");
          // IF SIGNED-IN SUCCESSFULLY    ******************************
          setTimeout(() => {
            this.$session.start();
            
            this.$session.set("id",res.data.live.replace("blac", ""));
            this.$session.set("firstname",res.data.firstname);
            this.$session.set("lastname",res.data.lastname);
            this.$session.set("username",res.data.username);
            this.$session.set("email",res.data.email);
            this.$session.set("address",res.data.address);
            this.$session.set("phone",res.data.phone);
            this.$session.set("country",res.data.country);

            if(this.signinForm.rememberMe) {
              let days = new Date().getTime() + (1000*60*60*24*15);
              let makingStr = `${ res.data.firstname }->${ res.data.live }`;
              let makingStr2 = btoa(makingStr) + "." + new Date().toUTCString();
              console.log(btoa(makingStr2));
              this.$cookies.set('tokenIn', btoa(makingStr2), new Date(days).toUTCString());
            }
            return;
            this.$router.push({name: this.$route.params.redirect ? this.$route.params.redirect : "home"});
          }, 3000);
        }
        
      }).catch((err) => {
        console.log("Sign-in error",err);
      });
    }
  },
  passwordPrompt() {
    const form = this.signupForm;
    // VALIDATION OF SUBMIT FORM  ********************
    // CHECK FOR ERRORS FIRST     ********************
    this.signupForm.errors = [];
    if(form.firstname == '') {
      this.signupForm.errors.push('0');
    }
    if(form.lastname == '') {
      this.signupForm.errors.push('1');
    }
    if(form.email == '') {
      this.signupForm.errors.push('2');
    }else {
      let emailPattern = /^[\dA-Z.a-z]+@[\da-z.A-Z]+[\da-zA-Z]+\.[\da-zA-Z]{0,5}$/;
      // console.log(form.email.search(emailPattern));
      this.signupForm.errors.push('2.5');
    }
    if(form.password == '') {
      this.signupForm.errors.push('3');
    }else if(form.confirmPassword == '') {
      this.signupForm.errors.push('3.4');
    }else if (form.password != form.confirmPassword) {
      this.signupForm.errors.push('3.8');
    }
    let col = ['#cc001c','#f16400','#fbe800','#c2d800','#24b500'];

    if(this.signupForm.errors.length > 0) return console.log(this.signupForm.errors);
  },
  focusPassword(e:Event) {
    this.signupCheckStrength = true;
    // this.errsBool.password = false;
  },
  blurPassword(e:Event) {
    this.signupCheckStrength = false;
    this.showing(['3','3.2','3.3'], 'password');
  },
  blurConfirmPassword(e:Event) {
    this.showing(['3.4', '3.8'], 'confirmPassword');
  },
  showing(errCode, inputName:string, force: boolean = false) {
    // this.returnErrMsg(errCode);
    if(!this.errsBool[inputName]) setTimeout(() => {
      this.errsBool[inputName] = false;
    },3000);
    // console.log(errCode, this.returnErrMsg(errCode), this.returnErrMsg(errCode) || force);
    this.returnErrMsg(errCode) || force ? this.errsBool[inputName] = true : null;
  },
  animateText(obj, str:string = "try", duration: number = 100) {
    let arrText = str.split("");
    let i = 0;
    obj.list = [];
    setTimeout(function loop() {
      if(i < arrText.length-1 && arrText[i] != "") {
        setTimeout(() => {
          loop();
          arrText[i] != '' && obj.list.push(arrText[i]);
          // console.log(self.testObj.list, arrText[i]);
          i++;
        }, duration);
      }
    }, 0);
  }
},
watch: {
  async 'signupForm.password'(newVal) {
    let chkStrength = await this.$doc(".form-password .check-strength") as HTMLElement;
    // console.log(chkStrength);
    let list1 = /.{8,}/; // MORE THAN EIGHT CHARACTERS
    let list2 = /(?=.*[A-Z]+)(?=.*[a-z]+)(?=.*[0-9]+)/; // AT LEAST, ONE OF THESE CHARACTERS SHOULD BE INCLUDED - uppercase,lowercase,digits
    let list3 = /[A-Z]+/; // AT LEAST, ONE uppercase CHARACTER SHOULD BE INCLUDED
    let list4 = /[a-z]+/; // AT LEAST, ONE lowercase CHARACTER SHOULD BE INCLUDED
    let list5 = /[0-9]+/; // AT LEAST, ONE digit CHARACTER SHOULD BE INCLUDED
    let ansSheet = []; // GROUPING THE MATCHED RESULTS
    let col = ['#cc001c','#f16400','#fbe800','#c2d800','#24b500'];
    if(newVal.search(list1) >= 0 && ansSheet.join("").search('1') == -1) ansSheet.push(1);
    if(newVal.search(list2) >= 0 && ansSheet.join("").search('2') == -1) ansSheet.push(2);
    if(newVal.search(list3) >= 0 && ansSheet.join("").search('3') == -1) ansSheet.push(3);
    if(newVal.search(list4) >= 0 && ansSheet.join("").search('4') == -1) ansSheet.push(4);
    if(newVal.search(list5) >= 0 && ansSheet.join("").search('5') == -1) ansSheet.push(5);
    // console.log(ansSheet, (ansSheet.length != 0 ? ansSheet.length - 1 : 0));
    chkStrength.style.width = (((ansSheet.length / 5) * 100) * ((newVal.length <= 8 ?newVal.length: 8)/8)) + '%';
    chkStrength.style.backgroundColor = col[(ansSheet.length != 0 ? ansSheet.length - 1 : 0)];
  },
  '$route'(route) {
    this.signup = route.name == "signup";
  },
  'signupForm.email'(email) {
    let formData = this.$extFD({
        username: email,
        identifier: 'email'
    });
    this.$axios.post(this.getServerDomain + "/users/get", formData).then((res) => {
      // console.log(res.data);
      if(res.data && res.data.length > 0) {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => !(error == '2.3'));
        this.signupForm.errors.push('2.3');
      }else this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => !(error == '2.3'));
    }).catch((err) => {
      console.log("Checking email existence", err);
    });
  },
  'signupForm.username'(username) {
    let formData = this.$extFD({
        username: username,
        identifier: 'username'
    });
    this.$axios.post(this.getServerDomain + "/users/get", formData).then((res) => {
      // console.log(res.data);
      if(res.data && res.data.length > 0) {
        this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => !(error == '5.5'));
        this.signupForm.errors.push('5.5');
      }else this.signupForm.errors = this.signupForm.errors && this.signupForm.errors.filter(error => !(error == '5.5'));
    }).catch((err) => {
      console.log("Checking username existence", err);
    });
  }
},
}
