export function erHTMLChild(arr:HTMLCollection, childClassname:string) {
  let returnVal = null;

  for(let i=0; i<arr.length; i++){
    arr[i].className.search(childClassname) >= 0 ? returnVal = arr[i] as HTMLElement : null;
    // console.log(arr[i].className);
  }
  return returnVal;
}
export function erTbcolumn(el:HTMLElement, binding:DirectiveBinding, vNode:VNode) {
  setTimeout(tableCalcus, 0);
  
  function tableCalcus() {
    let curClassname = el.getAttribute('class');
    if(curClassname && curClassname.search(binding.name) < 0) el.setAttribute('class', curClassname + ' ' + binding.name);
    else el.setAttribute('class', binding.name);
    
    let allCols = el.children;
    let numCols = null;
    numCols = "";

    if(allCols) {
      let getHei = binding.value ? binding.value.height : null;
      
      if(getHei) el.style.minHeight = getHei;
      else el.style.minHeight = "100vh";
    }

    /* C H E C K    I F   THCOL   E X I S T */
    for(let i=0; i<allCols.length; i++){
      let curCol = allCols[i] as HTMLElement;
      let curColClass = curCol.getAttribute('class');
      // console.log(curColClass);
      curColClass = curColClass ? curColClass : '';
      // let allAttr = curCol.attributes;
      let attr = curCol.attributes.getNamedItem('thcol');
      
      if(attr && attr.value != "*" && typeof attr.value == 'string') {
        
        if(!isNaN(+attr.value)) curCol.style.flexBasis = `${ attr.value }px`;


        if (curColClass && curColClass.search("thcol") < 0) curCol.setAttribute('class', curColClass + " thcol");
        else curCol.setAttribute('class', "thcol")
        
      }else if(attr && attr.value == "*") {
        if (curColClass && curColClass.search("thcol-auto") < 0) curCol.setAttribute('class', curColClass + " thcol-auto");
        else if (curColClass == '') curCol.setAttribute('class', "thcol-auto");
        else curCol.setAttribute('class', curColClass)
      }
    }/* END   C H E C K    I F   THCOL   E X I S T------ */
    
  }
}
export function erScroller(el:HTMLElement, binding:DirectiveBinding, vNode:VNode) {
  setTimeout(scrollerMeth, 0);
  
  async function scrollerMeth() {
    let classname = el.getAttribute('class');
    if(classname != null && classname.search(new RegExp(binding.name)) == -1) {
      el.setAttribute("class", `${ classname } ${ binding.name }`);
    }else if(classname == null) {
      el.setAttribute("class", `${ binding.name }`);
    }

    el.style.overflow = "auto";
    
    let val = binding.value;
    if(val) {
      // el.style.height = binding.arg..height;
      val.height != undefined ? el.style.height = val.height : null;
    }
    
    let secShow = 600;
    
    let scrollHeight = el.scrollHeight;
    let scrollTop = el.scrollTop;
    let height = el.clientHeight;
    let classnameScollbar = "pf-scroller";
    // let selScroller:HTMLElement | null = el.querySelector(`.${ classnameScollbar }`);
    let selScroller = await awaitTimeOut(() => erHTMLChild(el.children , classnameScollbar)) as HTMLElement;
    let topLimit = scrollHeight - height;
    let calScrollHeight = (height / scrollHeight) * height;
    let calTopPercentage = height - calScrollHeight;
    
    let calScrollTop = (scrollTop / topLimit) * calTopPercentage;
    
    // let heightNegate = (height / scrollHeight) * height;
    // heightNegate = heightNegate < 0 ? -heightNegate : heightNegate;
    // console.log(heightNegate);

    // let realScrollTop = (scrollTop / (scrollHeight-height)) * 100;
    // realScrollTop = ((scrollTop / (scrollHeight-height)) * (height - heightNegate)) + scrollTop;
    // if(isNaN(realScrollTop)) realScrollTop = 0;    
    
    /* A D D    S C R O L L   E V E N T   */
    el.removeEventListener("scroll", elScrollMethod);
    el.addEventListener("scroll", elScrollMethod);
    
    /* ****************************************** */
    let timeScrollVis:any;
    async function elScrollMethod(e:Event) {
      selScroller = await awaitTimeOut(() => erHTMLChild(el.children, classnameScollbar)) as HTMLElement;
      if(selScroller == null) attachScroller(false);
      scrollHeight = el.scrollHeight;
      scrollTop = el.scrollTop;
      height = el.clientHeight;
      topLimit = scrollHeight - height;
      calScrollHeight = (height / scrollHeight) * height;
      calTopPercentage = height - calScrollHeight;
      
      calScrollTop = (scrollTop / topLimit) * calTopPercentage;
      // console.log(height, scrollHeight, scrollTop, "=", height - scrollTop, scrollHeight-height);
      // heightNegate = (height / scrollHeight) * height;
      // heightNegate = heightNegate < 0 ? -heightNegate : heightNegate;
      // realScrollTop = ((scrollTop / (scrollHeight-height)) * (height - heightNegate)) + scrollTop;
      
      if(scrollHeight == height) {
        // return selScroller != null ? selScroller.style.display = 'none' : null;
        return selScroller && selScroller.remove();
      }else {
        selScroller != null ? selScroller.style.display = 'initial' : null;
      }
  
      selScroller != null ? selScroller.style.top = (calScrollTop + scrollTop) + 'px' : null;
      selScroller != null ? selScroller.style.height = calScrollHeight + 'px' : null;
      selScroller != null ? selScroller.style.opacity = "1" : null;
      selScroller != null ? selScroller.style.pointerEvents = "initial" : null;
      
      clearTimeout(timeScrollVis);
      
      timeScrollVis = setTimeout(async () => {
        selScroller != null ? selScroller.style.opacity = "0.01" : null;
        // console.log("clearTimeout");
        // selScroller && selScroller.remove();
      }, secShow);
      
    }
    window.addEventListener('resize', elScrollMethod);


    let mouseTiming = 0;
    let mousestilldown = false;
    let dis = {
      start: 0,
      end: 0,
      mea: 0,
      top: 0,
      duration: 0
    }
    window.addEventListener('mousemove', (e) => {
        
      if(dis.duration > 0 && mousestilldown) {
        // console.log(e);
        dis.end = e.clientY;
        dis.mea = dis.end - dis.start;

        el.scrollTop = dis.top + (dis.mea * ((el.scrollHeight/el.clientHeight) * 0.95));
      }
    });
    window.addEventListener('mouseup', (e) => {
      if(dis.duration && mousestilldown) {
        clearInterval(mouseTiming);
        // console.log(dis.duration);
        dis.duration = 0;
        mousestilldown = false;
        // console.log(dis);
      }
    });
  
    async function attachScroller(first = true) {
      /* A T T A C H    S C R O L L E R    */
      // selScroller = await awaitTimeOut(() => document.querySelector(".pf-scroller")) as HTMLElement;
      let elscroller = document.createElement('div');
      scrollHeight = el.scrollHeight;
      scrollTop = el.scrollTop;
      height = el.clientHeight;
      topLimit = scrollHeight - height;
      calScrollHeight = (height / scrollHeight) * height;
      calTopPercentage = height - calScrollHeight;
      
      calScrollTop = (scrollTop / topLimit) * calTopPercentage;
      
      elscroller.setAttribute('class', classnameScollbar);
      elscroller.style.position = 'absolute'; 
      elscroller.style.top = (calScrollTop + scrollTop) + 'px';
      elscroller.style.right = '3px';
      // elscroller.style.cursor = 'pointer';
      // elscroller.style.width = '5px';
      elscroller.style.paddingLeft = '8px';
      elscroller.style.paddingRight = '1px';
      elscroller.style.transition = "opacity 0.1s cubic-bezier(0.46,-0.01, 0, 1.08)";
      // let heightNegate = height - (scrollHeight - height);
      // let heightNegate = (height / scrollHeight) * 100;
      // heightNegate = heightNegate < 0 ? -heightNegate : heightNegate;
      elscroller.style.height = calScrollHeight + 'px';

      let elscrollerInner = document.createElement('div');
      elscrollerInner.style.borderRadius = '8px';
      elscrollerInner.style.position = "relative";
      elscrollerInner.style.width = "5px";
      elscrollerInner.style.height = "100%";
      elscrollerInner.style.backgroundColor = '#83818488';

      elscroller.insertAdjacentElement("beforeend", elscrollerInner);
      
      elscroller.addEventListener('mouseover', (e) => {
        clearTimeout(timeScrollVis);
        elscroller.style.opacity = "1";
        
      });
      elscroller.addEventListener('mouseout', (e) => {
        timeScrollVis = setTimeout(() => {
          elscroller.style.opacity = "0.01";
        }, secShow);
      });
      /* ---------------------------------------- */
      elscroller.addEventListener('mousedown', (e) => {
        e.preventDefault();
        dis.start = e.clientY;
        dis.top = el.scrollTop;
        dis.duration = 0;
        mouseTiming = setInterval(() => {
          elscroller.style.opacity = "1";
          dis.duration ++
          mousestilldown = true;
          // console.log(dis.duration);
        }, 1);

      });
      
      selScroller && selScroller.remove();
      el.insertAdjacentElement('beforeend', elscroller);

      if(scrollHeight != height) {
        elscroller.style.opacity = "1";
        
        setTimeout(async () => {
          elscroller.style.opacity = "0.01";
          // first && selScroller && selScroller.remove();
        }, secShow);
        elscroller.style.pointerEvents = "initial";
      }else {
        elscroller.style.opacity = "0";
        elscroller.style.pointerEvents = "none";
        // first && selScroller && selScroller.remove();
      }/* END - A T T A C H    S C R O L L E R    */
    }
    attachScroller();
  }
}
let timingTooltip = 0;
export async function tooltip(el:HTMLElement, binding:DirectiveBinding, vNode: VNode) {
  console.log(binding);
  clearTimeout(timingTooltip);
  await docSel('.check-strength');// WAIT FOR SETTIMEOUT THEN CONTINUE THE CODE   *******************
  
  console.log(document.querySelector(".check-strength"));
  // CREATE TOOLTIP ELEMENT   ************
  let classTooltip = 'er-tooltip';
  let classTooltipContainer = 'tooltip-contain';
  let tooltip = document.createElement('div');

  //  ): NICE-TRICK *** THIS ELEMENT WILL HELP POSITION THE TOOLTIP RIGHT AFTER THE EL    ****************
  let newDiv = document.createElement('div'); newDiv.setAttribute('class', classTooltipContainer);
  
  
  // PREPARE AND CREATE THE TOOLTIP   *************************
  let existingTooltip = el.nextElementSibling as HTMLElement;
  //  REMOVE ALL TOOLTIP CONTAINER
  let existingNewDiv = el.nextElementSibling as HTMLElement;
  let counter = 0;
  while(existingNewDiv && counter < 100) {
    existingNewDiv = el.nextElementSibling as HTMLElement;
    existingNewDiv && existingNewDiv.className == classTooltipContainer && existingNewDiv.remove();
    counter++;
  }

  tooltip.setAttribute('class', classTooltip);
  tooltip.innerText = binding.value && binding.value.msg ? binding.value.msg : `The message has arrived!`;
  
  // let existingTooltip = await awaitTimeOut(() => el.querySelector('.' + classTooltip)) as HTMLElement;
  console.log(existingTooltip.className , tooltip.className);
  timingTooltip = setTimeout(() => {
    let contain = document.querySelector("."+classTooltipContainer);
    contain && contain.remove();
  }, 3000);

  //  CREATE THE TOOLTIP STYLE ELEMENT    *****************************
  let tooltipStyle = document.createElement('style');
  tooltipStyle.setAttribute('id', classTooltip);
  tooltipStyle.setAttribute('type', 'text/css');
  tooltipStyle.textContent = `
    .${classTooltipContainer} {
      z-index: 99999;
    }
    .${classTooltip} {
      position: absolute;
      background-color: #f3f9fe;
      padding: 8px 22px 12px;
      right: 0;
      border-radius: 8px;
      top: -10px;
      font-size: 0.9em;
      box-shadow: 1px 2px 4px #6778a666;
    }
  `;
  
  let checkStyle = document.querySelector("style#"+classTooltip);
  !checkStyle && document.head.insertAdjacentElement('beforeend', tooltipStyle);
  console.log(checkStyle);
  
  // existingTooltip.className != tooltip.className && newDiv.insertAdjacentElement('beforeend', tooltip);
  // existingNewDiv.className != classTooltipContainer && el.insertAdjacentElement('afterend', newDiv)
  newDiv.insertAdjacentElement('beforeend', tooltip);
  el.insertAdjacentElement('afterend', newDiv)

}
export function erCheckImageSize(src: string, duration: number = 150)  {
  return new Promise((resolve, reject) => {
    let img = document.createElement('img');
    let classname = `img-${ new Date().getTime() }`;
    /* img.src = "http://localhost//web-files/proffilms-server/assets/ups/uploadimg/accessory-braids-close-up-2009395.jpg";
    img.src = "http://localhost//web-files/proffilms-server/assets/ups/uploadimg/adult-art-attractive-982610.jpg"; */
    img.src = src;
    // img.width = 100;
    img.style.position = 'absolute';
    img.style.visibility = 'hidden';
    img.setAttribute('class', classname);
    document.body.insertAdjacentElement('beforeend', img);

    let img2 = document.querySelector('.' + classname) as HTMLImageElement;
    let counter = 0;
    setTimeout(function looper() {
      img2 = document.querySelector('.' + classname) as HTMLImageElement;
      let data = {width:<any>null, height:<any>null};
      // SET THE DATA OBJECT FOR THE IMAGE  ..............................
      if(img2) {
        data = {
          width: img2.width,
          height: img2.height,
        }
      }else {
        reject('There seems to be an error with the image url!');
      }
      // IF THE IMAGE WIDTH CAN BE OBTAINED OR NOT  ...................
      if((data.width == 0 || data.height == 0) && counter < 200) {
        setTimeout(() => {
          looper();
          counter ++;
        }, 5);
      }else {
        if((data.width == 0 || data.height == 0) && counter >= 200) {
          reject('Still could not get the image Data! Please try again');
        }else {
          img2.remove();
          resolve(data);
        }

      }
    }, duration);

  });
}
export function erLooper(cb:Function, duration: number = 50) {
  setTimeout(function looper() {
    if(cb()) {
      setTimeout(() => {
        looper();
      }, duration);
    }
  }, 0);
}
export function awaitTimeOut(cb:CallableFunction, duration = 0) {
  return new Promise(resolve => setTimeout(() => {
    resolve(cb());
  }, duration));
}
export function docSel(name:string, duration = 0) {
  return new Promise(resolve => setTimeout(() => {
    let doc = document.querySelector(name) as HTMLElement;
    resolve(doc);
  }, duration));
}
export function seperator(val:string|number, seperator=",") {
  if(isNaN(+val)) return console.log("The value must not be empty and be a number | stringnumber.");
  val = val.toString();
  let leftover = val.split(".");
  
  if(val.length > 3) {
    let valArr = leftover[0].split('');
    let newArr = [];

    valArr.map((cur,i,arr) => {
      let pos = ((i - (arr.length % 3)) % 3);
      // console.log(i, pos, arr.length % 3);
      
      if(i != 0 && pos === 0 && i != arr.length-1 ) {
        newArr.push(seperator);
        newArr.push(cur);
      }else {
        newArr.push(cur);
      }
    })
    val = newArr.join("");
  }

  val = leftover.length > 1 ? `${val}.${leftover[1]}` : val;
  // console.log(val);
  return val;
}
export function extractFormData(obj:object) {
  let theList = Object.entries(obj);
  let formData = new FormData();
  theList.forEach((val) => {
    formData.append(val[0], val[1])
  });
  return formData;
}