
























































import { mapActions } from 'vuex';

export default {
mounted() {
  this.setStockItems().then((sales) => {
    this.salesItems = sales;
    console.log(sales);
  }).catch((err) => {
    
  });
},
data: () => ({
  stockForm: {
    item: '',
    identity: '',
    quatity: 0,
    unitprice: 0,
    createdon: null

  },
  salesItems: []
}),
methods: {
  ...mapActions(['setStockItems','returnSales']),
  async addUpdate(e:Event) {
    let sendData = this.stockForm;
    if(sendData.item != "" && sendData.identity != "" && +sendData.quatity > 0 && sendData.unitprice > 0) {
      // IF THE SEND-DATA IS NOT EMPTY, INSERT INTO THE STOCKS
      sendData.createdon = new Date().getTime();
      this.setStockItems(sendData).then((stock) => {
        console.log("Stock", stock);
      }).catch((err) => {
        console.log("Set Stock", err);
      });
    }else {
      
      console.log("Only Get the Data: ", await this.setStockItems());
    }

  }
},
watch: {
  '$route.name'(name) {
    this.setStockItems().then((sales) => {
    this.salesItems = sales;
    console.log("Manage: ",sales);
  }).catch((err) => {
    
  });  
  }
},
}
