Vue.mixin({
  methods: {
    formatDate(date) {
      let daysArr = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
      let mnthArr = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
      let lngMnthArr = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
      let dateObj = null;
      
      if(typeof date == 'number' || typeof date == 'string') {
        dateObj = new Date(+date);
        
      }else if(typeof date == typeof new Date() && date != null) {
        dateObj = date;
      }else{
        console.log("Please input a valid Date: String | Number | Date Object");
        return "Please input a valid Date: String | Number | Date Object";
      }
      
      if(new Date(dateObj).toString() == "Invalid Date") return {
        day: "",
        month: "",
        year: "",
        time: 0,
        dw: ""
      };
      let yr = dateObj.getUTCFullYear();
      let mnth = lngMnthArr[dateObj.getUTCMonth()];
      let day = dateObj.getUTCDate();
      // day = '322';
      let lastLetter = day.toString().slice(day.toString().length-1);
      let firstLetter = day.toString().slice(0,1);
      

      if((lastLetter == 0 && firstLetter > 0) || (lastLetter > 3 && day.toString().length >= 2) || (day.toString().length >= 2 && firstLetter == 1)){
        // day += '<sup>th</sup>';
        day += "th"
      }else if(parseInt(day.toString()) != 0) {
        switch (lastLetter) {
          case '1':
            // day += '<sup>st</sup>';
            day += 'st';
            break;
          case '2':
            // day += '<sup>nd</sup>';
            day += 'nd';
            break;
          case '3':
            // day += '<sup>rd</sup>';
            day += 'rd';
            break;
        
          default:
            day += "th"
            break;
        }
      }

      return {
        day: day,
        month: mnth,
        year: yr,
        time: dateObj.getTime(),
        dw: daysArr[dateObj.getDay()]
      }
      
      
      if(typeof date == 'number' || typeof date == 'string') {

      }else if(typeof date == typeof Date) {

      }
    },
    sorting(type, data = null) {
      switch (type) {
        case "date":
          data.sort((b,a) => {
            return new Date(a.date).getTime() > new Date(b.date).getTime() ? 1 : -1;
          });
          break;
        case "name":
          data.sort((b,a) => {
            return a.name > b.name ? 1 : -1;
          });
          break;
      
        default:
          data.sort((b,a) => {
            return +a[type] > +b[type] ? 1 : -1;
          });
          break;
      }
      
    }
  },
});

